/* 전역 스타일 초기화 */
@font-face {
  font-family: 'LINESeedKR-Bd';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: LINESeedKR-Bd;
  line-height: 1.6;
}

.main {
  width: 80%;
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.intro {
  text-align: center;
  margin-bottom: 40px;
}

.intro h1 {
  font-size: 2.5rem;
  color: #333;
}

.intro p {
  font-size: 1.2rem;
  color: #555;
}

.skills, .education, .projects, .etc {
  margin-bottom: 40px;
}

.skills h1, .education h1, .projects h1, .etc h1 {
  font-size: 2rem;
  border-bottom: 2px solid #333;
  padding-bottom: 10px;
  padding-left: 2%;
  margin-bottom: 40px;
  color: #222;
}

.skills table {
  width: 100%;
  border-collapse: collapse;
}

.skills td {
  padding: 10px;
}

.skills .title {
  font-size: large;
  font-weight: bold;
  width: 30%;
}

.skills tr:nth-child(odd) {
  background-color: #ffffff;
}

.skills tr:nth-child(even) {
  background-color: #d3d3d3; /* 밝은 회색 코드 */
}

.skills ul {
  list-style-type: none;
}

.skills ul li {
  padding: 5px 0;
}

.education-content {
  margin-bottom: 100px;
}

.education-content p {
  margin: 5px 0;
}

.college {
  font-size: 1.2rem;
  font-weight: bold;
}

.edu-details {
  display: flex;
  justify-content: space-between;
}

.projects-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  gap: 20px; /* 요소 사이의 간격을 설정 (필요에 따라 조절) */
}

.projects-section {
  flex : 1;
  margin-bottom: 40px;
}

.projects-section h2 {
  font-size: 1.5rem;
  border-bottom: 2px solid #333;
  padding-bottom: 10px;
  margin-bottom: 20px;
  color: #222;
  width: 10%;
}

.projects-list p {
  cursor: pointer;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.projects-list p:hover {
  background-color: #f0f0f0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 80%;
  max-height: 80%;
  overflow: auto; /* 스크롤이 필요할 때 스크롤바를 표시합니다 */
  position: relative;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  font-weight: bold;
}

.images {
  margin-top: 20px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.images .container {
  flex: 1 1 calc(50% - 16px); /* 두 열로 나누되, 간격을 고려한 너비 설정 */
  box-sizing: border-box; /* 패딩과 테두리가 너비에 포함되도록 설정 */
}

.images img {
  width: 100%; /* 이미지가 컨테이너 너비에 맞게 확장 */
  height: auto; /* 비율을 유지하면서 높이를 자동 조정 */
}

.container {
  margin-bottom: 20px;
}

img {
  display: block;
  margin: 0 auto;
}

.codes{
  width: 100%;
}

.code-content {
  font-family: 'Courier New', Courier, monospace; /* 기본 모노스페이스 글꼴 */
  font-size: 14px; /* 글꼴 크기 조절 */
  color: #333; /* 글꼴 색상 조절 */
  background-color: #f5f5f5; /* 배경 색상 조절 */
  padding: 10px; /* 여백 추가 */
  border-radius: 5px; /* 모서리 둥글게 */
  white-space: pre-wrap; /* 긴 텍스트 줄 바꿈 */
}